import React, { useEffect, useState } from 'react'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, Copy, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, InputGroup, InputGroupAddon, InputGroupText, CustomInput
} from 'reactstrap'
import useJwt from '@src/auth/jwt/useJwt'
import { Error, Success, ErrorMessage } from '../../viewhelper'
import { useHistory } from 'react-router-dom'
import Select from 'react-select'
import { selectThemeColors, transformInToFormObject } from '@utils'
import InputPasswordToggle from '@components/input-password-toggle'

const Create = () => {
    const history = useHistory()
    const [featureIDs, setFeatureIDs] = useState([])
    const [addUserloading, setaddUserloading] = useState(false)
    const [allMenuList, setallMenuList] = useState([])
    const [userTypes, setuserTypes] = useState([])
    const [userTitles, setuserTitles] = useState([])
    const [userInput, setUserInput] = useState({
        username: "",
        password: '',
        roleid: '',
        title: '',
        userstatus: '',
        emailid: ''
    })

    useEffect(() => {
        useJwt.getAdminMenuSubmenuList().then(res => {
            // setaddUserloading(false)
            console.log('getAdminMenuSubmenuList', res)
            setallMenuList(res.data.payload)
            // Success(res)
        }).catch(err => {
            // setaddUserloading(false)
            Error(err)
            console.log(err)
        })
        useJwt.getUsertittleList().then(res => {
            // setaddUserloading(false)
            console.log('getUsertittleList', res)
            setuserTitles(res.data.payload)
            // Success(res)
        }).catch(err => {
            // setaddUserloading(false)
            Error(err)
            console.log(err)
        })
    }, [])
    const [errors, seterrors] = useState({
        title: '',
        userstatus: '',
        email: ''
    })

    const onchange = (e) => {
        if (e.target.name === 'emailid' && /\S+@\S+\.\S+/.test(e.target.value)) {
            seterrors({ ...errors, email: '' })
        }
        setUserInput({ ...userInput, [e.target.name]: e.target.value })
    }

    const onSubmit = (e) => {
        e.preventDefault()
        const { username, title, userstatus, password, emailid } = userInput
        const checkingEmail = /\S+@\S+\.\S+/.test(emailid)
        if (!checkingEmail) {
            seterrors({ ...errors, email: 'is not valid' })
            return 0
        }
        if (title === '') {
            seterrors({ ...errors, title: 'is required' })
            return 0
        }
        if (userstatus === '') {
            seterrors({ ...errors, userstatus: 'is required' })
            return 0
        }
        console.log({ username, emailid, password, roleid: 12, title, userstatus, sub_menu_ids: [...new Set(featureIDs)] })
        setaddUserloading(true)
        useJwt.createAdminUser({ username, emailid, password, roleid: 12, title, userstatus, sub_menu_ids: [...new Set(featureIDs)] }).then(res => {
            setaddUserloading(false)
            console.log(res)
            Success(res)
        }).catch(err => {
            setaddUserloading(false)
            Error(err)
            console.log(err.response)
        })
    }
    return (
        <>
            <Form style={{ width: '100%' }} onSubmit={onSubmit} autoComplete="off">
                <Card>
                    <CardHeader className='border-bottom'>
                        <CardTitle tag='h4'>Add New User</CardTitle>
                    </CardHeader>
                    <CardBody>
                        <Row className="pt-1" >
                            <Col md='4' >
                                <FormGroup>
                                    <Label for="username">Username <span style={{ color: 'red' }}>*</span></Label>
                                    <Input type="text"
                                        name="username"
                                        id='username'
                                        value={userInput.username}
                                        onChange={onchange}
                                        required
                                        placeholder="Jhon@123"
                                    />
                                </FormGroup>
                            </Col>
                            <Col md='4' >
                                <FormGroup>
                                    <Label for="emailid">Email <span style={{ color: 'red' }}>* {errors.email}</span></Label>
                                    <Input type="text"
                                        name="emailid"
                                        id='emailid'
                                        value={userInput.emailid}
                                        onChange={onchange}
                                        required
                                        placeholder="Jhon_doe@gmail.com"
                                    />
                                </FormGroup>
                            </Col>
                            <Col md='4' >
                                <FormGroup>
                                    <Label for="password">Password <span style={{ color: 'red' }}>*</span></Label>
                                    <InputPasswordToggle
                                        name="password"
                                        id='password'
                                        value={userInput.password}
                                        onChange={onchange}
                                        required
                                        minLength='4'
                                        placeholder="******"
                                    />
                                    {/* {error.password && userInput.password && <span style={{ color: 'red', fontSize: '11px' }}>must be greater than 4 digit</span>} */}
                                </FormGroup>
                            </Col>

                            {/* <Col md='4' >
                                <Label>User Type <span style={{ color: 'red' }}>* {errors.userstatus}</span></Label>
                                <Select
                                    theme={selectThemeColors}
                                    className='react-select'
                                    classNamePrefix='select'
                                    onChange={(selected) => {
                                        seterrors({ ...errors, userstatus: '' })
                                        setUserInput({ ...userInput, userstatus: selected.value })
                                    }}
                                    options={userTypes}
                                    // options={[{ value: 1, label: "Active" }, { value: 0, label: "Inactive" }]}
                                    isClearable={false}
                                />

                            </Col> */}
                            <Col md='4' >
                                <Label>User Title <span style={{ color: 'red' }}>* {errors.title}</span></Label>
                                <Select
                                    theme={selectThemeColors}
                                    className='react-select'
                                    classNamePrefix='select'
                                    onChange={(selected) => {
                                        seterrors({ ...errors, title: '' })
                                        setUserInput({ ...userInput, title: selected.value })
                                    }}
                                    options={userTitles.map(x => { return { value: x.id, label: x.statusdesc } })}
                                    isClearable={false}
                                />

                            </Col>
                            <Col md='4' >
                                <Label>Status <span style={{ color: 'red' }}>* {errors.userstatus}</span></Label>
                                <Select
                                    theme={selectThemeColors}
                                    className='react-select'
                                    classNamePrefix='select'
                                    onChange={(selected) => {
                                        seterrors({ ...errors, userstatus: '' })
                                        setUserInput({ ...userInput, userstatus: selected.value })
                                    }}
                                    options={[{ value: 1, label: "Active" }, { value: 0, label: "Inactive" }]}
                                    isClearable={false}
                                />

                            </Col>
                        </Row>
                    </CardBody>
                </Card>
                <Card >
                    <CardHeader className='border-bottom'>
                        <CardTitle tag='h4'>Permissions</CardTitle>
                        <CardTitle tag='h4'><CustomInput
                            type='checkbox'
                            id={'All'}
                            label={'Select All'}
                            inline
                            // checked={featureIDs.includes(feature.id)}
                            onChange={e => {
                                const Array2D = allMenuList.map(x => x.submenu.map(y => y.id))
                                e.target.checked ? setFeatureIDs([].concat(...Array2D)) : setFeatureIDs([])
                            }}
                        /></CardTitle>
                    </CardHeader>
                    <CardBody className='pt-1 pb-0'>
                        <Row className='match-height'>
                            {allMenuList.map(section => <Col md='4' key={section.id}>
                                <Card className='border'>
                                    <CardHeader className='border-bottom p-1'>
                                        <div className="d-flex justify-content-between w-100">
                                            <p className='mb-0 pb-0'>{section.name}</p>
                                            <CustomInput
                                                type='checkbox'
                                                id={section.id + 1000}
                                                label={'All'}
                                                inline
                                                checked={allMenuList.filter(x => x.id === section.id)[0].submenu.map(submenu => submenu.id).every(x => featureIDs.includes(x))}
                                                onChange={e => {
                                                    const AllSubmenuIDs = allMenuList.filter(x => x.id === section.id)[0].submenu.map(submenu => submenu.id)
                                                    // console.log('AllSubmenuIDs', AllSubmenuIDs)
                                                    const removedID = featureIDs.filter(x => AllSubmenuIDs.indexOf(x) < 0)
                                                    e.target.checked ? setFeatureIDs([...featureIDs, ...AllSubmenuIDs]) : setFeatureIDs(removedID)
                                                }}
                                            />
                                        </div>
                                    </CardHeader>
                                    <CardBody className='pt-1'>
                                        <Row>
                                            {section.submenu.map(feature => <Col sm='12' key={feature.id} className='my-0'>
                                                <CustomInput
                                                    type='checkbox'
                                                    id={feature.id}
                                                    label={feature.name}
                                                    inline
                                                    checked={featureIDs.includes(feature.id)}
                                                    onChange={e => {
                                                        // alert(feature.id)
                                                        // console.log(featureIDs)
                                                        e.target.checked ? setFeatureIDs([...featureIDs, feature.id]) : setFeatureIDs([...featureIDs.filter(previous => previous !== feature.id)])
                                                    }}
                                                />
                                            </Col>)}
                                        </Row>
                                    </CardBody>
                                </Card>
                            </Col>)}
                        </Row>
                    </CardBody>
                </Card>
                <Card >
                    <CardBody className='pt-0'>
                        <Row>
                            <Col sm="12" className='text-center'>
                                {
                                    addUserloading ? <Button.Ripple color='primary' className='mr-1' disabled style={{ marginTop: '25px' }}>
                                        <Spinner color='white' size='sm' />
                                        <span className='ml-50'>Loading...</span>
                                    </Button.Ripple> : <Button.Ripple className='ml-2' color='primary' type="submit" style={{ marginTop: '25px' }}>
                                        <span >Submit</span>
                                    </Button.Ripple>
                                }
                            </Col>
                        </Row>
                    </CardBody>
                </Card>
            </Form>
        </>
    )
}

export default Create